import ConnectPlugin from "@/Components/connectPlugins";
import { GetAddonsList } from "@/utilities/cookies";
import { notFound } from "next/navigation";

import createMetadata from "@/Components/Seo/createMetadata";

export async function generateMetadata() {
  const title = "Quotation";
  return createMetadata({
    title: title,
  });
}

export default async function Quotationsucess() {
  const addonList = await GetAddonsList();

  if (addonList && addonList?.["product-quotation"]?.status == 0) {
    notFound();
  }

  if (!addonList?.["product-quotation"]) {
    notFound();
  }

  return (
    addonList &&
    addonList?.["product-quotation"]?.status == 1 &&
    ConnectPlugin.QuatationMessage && <ConnectPlugin.QuatationMessage />
  );
}
